<template>
  <div id="phone">
    <common-header title="提现"></common-header>
    <div class="phone__input">
      <input type="text" placeholder="请输入收到的验证码" v-model="code">
      <span class="btn" :class="{'sended': sended}" @click="getSmsCode" >{{btnText}}</span>
    </div>
    <div class="phone__info" v-show="sended">验证码已发送至手机：{{mobile}}</div>
    <div class="finish__btn" :class="{'active': isActive}" @click="finish">完成</div>
  </div>
</template>


<script type="text/javascript">
  import md5 from 'blueimp-md5';
	
	export default{
		data(){
			return{
				sended:false,
				mobile:'',
				checkKey:'',
				code:'',
        btnText: '获取验证码',
				
			}
		},
		created(){
//    	let UserDetails = JSON.parse( localStorage.getItem('UserDetails') )
//				this.mobile = UserDetails.mobile
		},
		computed:{
			isActive(){
				if(this.code.length > 0){
					return true
				}else{
					return false
				}
			}
		},
		methods:{
			getSmsCode(){
				if(this.sended) {
          return false;
        }
        this.sended = true;
        let count = 60;
        for(let i = count; i >= 0; i--) {
          setTimeout(() => {
            this.btnText = i + 's';
            if(i == 0) {
              this.btnText = '重新获取'
              this.sended = false;
            }
          }, (count - i - 1) * 1000)
        }

				let param = {
          a: 1,
          type: 8,
//        mobile: this.mobile,
        }
				this.$Api.SMSCode(param).then((res)=>{
					if(res.q.s == 0){
						this.checkKey = res.q.checkKey
						this.mobile = res.q.mobile
					}else{
						this.$toast(res.q.d)
					}
				})
			},
			finish(){
      	let UserDetails = JSON.parse( localStorage.getItem('UserDetails') )
				let param = {
          a: 2,
          type: 8,
//        mobile: this.mobile,
          checkKey: this.checkKey,
          code:this.code
        }
				this.$Api.SMSCode(param).then((res)=>{
					if(res.q.s == 0){
						this.getCashApplySubmit()
					}else{
						this.$toast(res.q.d)
					}
				})
			},
			getCashApplySubmit(){
				let accountObj = this.$route.params.params
				let params = {
					amount:accountObj.price,
     			bankId:accountObj.accountBankId,
     			account:accountObj.accountIdCode,
     			accountName:accountObj.accountName,
     			password:md5(this.$paykey + md5(accountObj.password))
				}
				this.$Api.CashApplySubmit(params).then((res)=>{
					console.log(res)
					if(res.q.s == 0){
						this.$toast(res.q.d)
						setTimeout(()=>{
							this.$router.replace({name:'Wallet'})
						},1500)
					}else{
						this.$toast(res.q.d)
					}
				})
			}
		}
	}
</script>

<style lang="scss">
  #phone {
    .phone__input {
      position: relative;
      margin: 0 0.4rem;
      line-height: 1.2rem;
      height: 1.2rem;
      border-bottom: 1px solid #eee;

      .btn {
        position: absolute;
        top: 0.2rem;
        right: 0;
        width: 2.53rem;
        height: 0.8rem;
        line-height: 0.8rem;
        text-align: center;
        color: #fff;
        background: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
        border-radius: 0.8rem;

        &.sended {
          background: #ccc;
        }
      }
    }

    .phone__info {
      padding: 0 0.4rem;
      padding-top: 0.3rem;
      color: #333;
    }
    
  }
</style>



